
<template>
  	<div class="reservation">
		<!-- <div class="header">
			<datepicker v-model="selectDate" :language="langKo" format="yyyy-MM-dd"></datepicker>
		</div> -->
		<FullCalendar 
		 :options="calendarOptions"
		/>

		<!-- ADD EVENT -->
		<vs-prompt
			class="calendar-event-dialog position-relative"
			title="새로운 예약"
			accept-text= "예약 추가"
			cancel-text= "취소"
