					<div class="con-vs-dropdown--menu vs-dropdown-menu" v-show="colorLabelPp">
						<div class="vs-dropdown--custom vs-dropdown--menu" style="z-index: 200001">
							<div class="vs-component-link-item">
								<div v-for="(label, index) in calendarLabels" :key="index" @click="clickLabel(label)" class="vs-component vs-dropdown--item cursor-pointer">
									<div class="vs-dropdown--item-link">
										<div class="h-3 w-3 inline-block rounded-full"  :style="`background-color: ${label.color}`"></div>
									</div>
								</div>
							</div>
							<div class="vs-dropdown--menu--after" style="top: 10px;"></div>
						</div>
					</div>
